:root {
    --bgLight: rgb(235, 234, 255);
    --bgDark: rgb(26, 24, 85);
}
.container{
    margin-top: 0 !important;
}
.bgLight{
   background-color: var(--bgLight); 
}
.bgDark{
    background-color: var(--bgDark);
}
.onLightFocus:focus{
    background-color: rgb(217, 215, 252);
    outline: none;
}
.textarea{
    width: 100%;
    padding: 10px;
}
.onDarkFocus:focus{
    background-color: rgb(82, 80, 141);
    color: white;
    outline: none;
}   
.heading h2{
    font-size: 35px;
    font-weight: bold;
}
.heading h4{
    font-size: 35px;
}

.lightText{
    color: black;
}
.DarkText{
    color: rgb(255, 255, 255);
}
.light-border{
    border: 3px solid black;
}
.dark-border{
    border: 3px solid rgb(255, 255, 255);
}
.button{
    display: flex;
    align-items: center;
    justify-content:space-around;
    flex-wrap: wrap;
}
.button button{
    background-color: rgb(58, 54, 62);
    color: white;
    border: none;
    margin: 2px;
    padding: 5px 30px;
    cursor: pointer;
}
.lightBtnBg{
    background-color: rgb(223, 223, 223) !important;
    color: black !important;
}
.DarkBtnBg{
    background-color: rgb(58, 54, 62) !important;
    color: rgb(255, 255, 255) !important;
}
.button button:hover{
    background-color: rgb(0, 0, 0);
}
.hrtop{
    display: none;
}


@media only screen and (max-width: 500px){
    .button{
        justify-content:space-around;
    }
    .button button{
        width: 48%;
        font-size: 15px;
        justify-content: center;
        align-items: center;
        padding: 10px 10px;
        
    }
    .heading h2{
        font-size: 30px;
        font-weight: bold;
        text-align: center;
    }
    .heading h4{
        font-size: 20px;
        text-align: center;
    }
    .hrtop{
        display: block;
    }
}
