.Error{
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}
.Error p{
    font-size: 20px;
}
.Exclamation{
    background-color: rgb(0, 0, 0);
    color: rgb(250, 250, 250);
    text-align: center;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    margin-right: 10px;
}

@media only screen and (max-width: 500px){
    .Exclamation{
        width: 20px;
        height: 20px;
    }
    .Error p{
        font-size: medium;
    }
}