:root {
    --lightBG: rgb(239, 239, 255);
    --DarkBG: rgb(11, 10, 59);
}

nav{
    display: flex;
    justify-content:space-between;
    align-items: center;
    padding: 20px 10px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.4);
    position: sticky;
    top:0;
    width: 100%;
}
.TextUtilsLogo{
    height: 50px;
}

.form-check-input{
    cursor: pointer;
}

.NavLight{
    background-color: var(--lightBG);
    font-size: 20px;
    font-weight: bold;
}
.NavDark{
    background-color: var(--DarkBG);
    font-size: 20px;
    font-weight: bold;
}
.TitleLight{
    color: black;
}
.TitleDark{
    color: rgb(255, 255, 255);
}
.icontext{
    font-size: 15px !important;
    margin: 5px;
}
@media only screen and (max-width: 500px){
    .NavLight{
        font-size: 20px;
    }
    .NavDark{
        font-size: 20px;
    }
    nav{
        padding: 20px 10px;
    }
}